.header {
    height: 60px;
    display: flex;
    align-items: center;
    background-color: rgb(3, 99, 3);
    position: sticky;
    top: 0;
    z-index: 100;
}

.header__logo {
    width: 100px;
    height: 60px;
    object-fit: contain;
    margin: 0 0px;
    margin-top: 5px;
}

.header__image {
    display: flex;
    font-size: 20px;
    font-weight: 800;
    align-items: center;
    color: rgb(241, 94, 94);
    margin-left: 10px;
    
}

.header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header__searchIcon {
    padding: 5px;
    height: 22px !important;
    background-color: rgb(241, 94, 94);
}

.header__optionLineOne {
   font-size: 10px; 
}

.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket {
    display: flex;
    align-items: center;
    color: rgb(241, 94, 94);
}
.header__basketCount {
    margin-left: 10px;
    margin-right: 10px;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}